// utils logic etc

import pattern from 'patternomaly';
import Chart from 'chart.js';

function createOrUpdateChart(chartId, chartData, myChart) {
  const ctx = document.getElementById(chartId);
  if (ctx === undefined) {
    throw ('ctx undef')
  }
  if (myChart == undefined) {
    myChart = new Chart(ctx, {
      type: chartData.type,
      data: chartData.data,
      options: chartData.options,
    });
  } else {
    console.log('only update chart!')
    // assume only data changes for now
    myChart.data = chartData.data
    // _.debounce(myChart.update, 1)
    myChart.update(0) // duration zero to have minimal jitter
  }
  return myChart // don't strictly need but eslint complains unused if not returned
}

function tax_calc(t, Xmean, R0, alpha, beta, F, G) {
  // t; Xmean; R0; alpha; beta; F; G;
  if (alpha.length != t) throw ('bad length')
  if (beta.length != t) throw ('bad length')
  var state = {
    X: [], // gross incomes
    R: [R0], // pension Room remaining at the start of each tax year, will be one longer than the rest
    C: [], // pension Constributions in each year (for convenience)
    T: [], // taxes
  }
  var X_total = Xmean * t
  var x, c_max, c;
  for (let i = 0; i < t; i++) {
    x = alpha[i] * X_total
    X_total = X_total - x
    state.X.push(x)
    // the max you can contribute given income (can not contribute more than income)
    c_max = Math.min(x, state.R.slice(-3).reduce((x, y) => x + y) + G(x))
    c = beta[i] * c_max
    state.C.push(c)
    // you can only use old room by over-contributing
    // so you only have room remaining if you don't over-contribute this year i.e. if c < G(x)
    state.R.push(Math.max(0, G(x) - c))
    state.T.push(F(x - c))
  }
  return state
}

function assert(condition, message) {
  if (!condition) {
    message = message || "Assertion failed";
    if (typeof Error !== "undefined") {
      throw new Error(message);
    }
    throw message; // Fallback
  }
}

var x_max = 10000000 // some big number to make the interp work

var split_xy = function (data) {
  return { x: data.map(xx => xx[0]), y: data.map(xx => xx[1]) }
}

function get_tax_funcs(taxinfo_selected) {
  var xy = split_xy(taxinfo_selected['data_income_tax']['data']) // ignore the type for now, it is fixed
  var F = get_tax_interp(xy.x, xy.y) // returns function and marginal
  var G_xy = split_xy(taxinfo_selected['data_pension_limit']['data'])
  var G = get_pension_limit(G_xy.x, G_xy.y)

  // taxes under max pension contrib, need to blend the x's and create a new y ... more complicated
  // var xx = merge_array(xy.x, G_xy.x)
  var xx = xy.x.map(x => x + G.G(x)) // shift all the normal points to the right because that is where things change
  // xx = merge_array(xx, xy.x) // dunno add lots of points
  xx = merge_array(xx, G_xy.x) // probably enough points
  xx.push(250000) // ugh hard code bad here now
  xx.push(x_max) // some big number for the interp
  var yy = xx.map(x => F.F(Math.max(0, x - G.G(x))))
  var FG = get_tax_interp2(xx, yy)

  var obj = {
    m_F: F.m_F, F: F.F, x: xy.x, y: xy.y,
    G: G.G, G_x: G_xy.x, G_y: G_xy.y,
    m_FG: FG.m_F, FG: FG.F, FG_x: xx, FG_y: yy
  }
  return obj
}

function get_pension_limit(x, y) {
  // basically just pw linear
  // the amount of pension room you have in a given year
  assert(x[0] == 0, "x[0] should be zero got " + x[0])

  x = x.slice()
  y = y.slice()
  x.unshift(0) // insert zero at the beginning to have flat extrap to the left
  y.unshift(y[y.length - 1])
  x.push(x_max)
  y.push(y[y.length - 1]) // flat extrap on the right as well

  var G = function (xx) {
    return PiecewiseLinear(x, y)(xx)
  }
  return { G: G }
}

function get_tax_interp2(x, y) {
  // this one tax x, y as total tax and computes the marginal from that
  // nothing to do to compute F, just compute m_F ?
  assert(y[0] == 0, "y[0] should be zero got " + y[0])
  assert(x[0] == 0, "x[0] should be zero got " + x[0])

  x = x.slice() // copy, TODO check on this
  y = y.slice()

  var marginals = [0]; // cumulative of tax paid up to x
  for (let i = 0; i <= x.length - 2; i++) {
    marginals.push( (y[i+1] - y[i]) / (x[i+1] - x[i]))
  }
  var m_F = function (xx) {
    return PiecewiseConstant(x, marginals)(xx)
  }

  var F = function (xx) {
    return PiecewiseLinear(x, y)(xx)
  }
  return { m_F: m_F, F: F, x: x, y: y}
}

function get_tax_interp(x, y) {
  // x is income, y is lhs marginal rate
  // marginal and total tax functions
  // returns two functions
  assert(y[0] == 0, "y[0] should be zero got " + y[0])
  assert(x[0] == 0, "x[0] should be zero got " + x[0])

  x = x.slice() // copy, TODO check on this
  y = y.slice()
  x.push(x_max)
  y.push(y[y.length - 1])

  var tax = [0]; // cumulative of tax paid up to x
  for (let i = 0; i <= x.length - 2; i++) {
    var dtax = (x[i + 1] - x[i]) * y[i]
    tax.push(dtax + tax[i])
  }
  var m_F = function (xx) {
    return PiecewiseConstant(x, y)(xx)
  }

  var F = function (xx) {
    return PiecewiseLinear(x, tax)(xx)
  }
  return { m_F: m_F, F: F, x: x, y: y}
}

