function PiecewiseConstant(xs, ys) {
  return function (x) {
    var lo = 0, hi = xs.length - 1;
    // bisection
    while (hi - lo > 1) {
      var mid = (lo + hi) >> 1; // bitwise shift (div by 2)
      if (x < xs[mid]) hi = mid;
      else lo = mid;
    }
    return ys[lo]
  };
}

function PiecewiseLinear(xs, ys) {
  return function (x) {
    var lo = 0, hi = xs.length - 1;
    // bisection
    while (hi - lo > 1) {
      var mid = (lo + hi) >> 1;
      if (x < xs[mid]) hi = mid;
      else lo = mid;
    }
    return ys[lo] + (ys[hi] - ys[lo]) / (xs[hi] - xs[lo]) * (x - xs[lo]);
  };
}

function repeat(n, value) {
  return Array.apply(null, Array(n)).map(function () { return value })
}

function linspace(startValue, stopValue, cardinality) {
  var arr = [];
  var currValue = startValue;
  var step = (stopValue - startValue) / (cardinality - 1);
  for (let i = 0; i < cardinality; i++) {
    arr.push(currValue + (step * i));
  }
  return arr;
}

function merge_array(array1, array2) {
  var result_array = [];
  var arr = array1.concat(array2);
  var len = arr.length;
  var assoc = {};

  while (len--) {
    var item = arr[len];

    if (!assoc[item]) {
      result_array.unshift(item);
      assoc[item] = true;
    }
  }
  return result_array.sort((x, y) => x - y);
}

function limit_enforce_x_max(arr, x_max) {
  var result_array = [];
  var len = arr.length;
  var assoc = {};
  while (len--) {
    var item = arr[len];
    if ((item < x_max) && (!assoc[item])) {
      result_array.unshift(item);
      assoc[item] = true;
    }
  }
  result_array.push(x_max)
  return result_array.sort((x, y) => x - y);
}

function chart_data_builder(obj, x_max_plot, xlabel, show_patterns) { // x, y, m_y, G_x, G_y, xlabel, show_patterns) {
  // builds the data for the charts based on F, G, FG etc

  // TODO limit x ranges ot x_max_plot

  // set up ranges
  var x = obj.x.slice()
  x = limit_enforce_x_max(x, x_max_plot)
  var G_x = obj.G_x.slice()
  G_x = limit_enforce_x_max(G_x, x_max_plot)
  var FG_x = obj.FG_x.slice()
  FG_x = limit_enforce_x_max(FG_x, x_max_plot)

  var y = x.map(obj.F)
  var m_y = x.map(obj.m_F)
  var G_y = G_x.map(obj.G)
  var FG_y = FG_x.map(obj.FG)
  var m_FG_y = FG_x.map(obj.m_FG)

  var data = [] // gross tax no pension contrib
  for (let i = 0; i < x.length; i++) {
    data.push({ x: x[i], y: y[i] })
  }
  var m_data = []
  for (let i = 0; i < x.length; i++) {
    m_data.push({ x: x[i], y: m_y[i] })
  }
  var G_data = [] // pension room
  for (let i = 0; i < G_x.length; i++) {
    G_data.push({ x: G_x[i], y: G_y[i] })
  }
  var data_p = [] // gross tax max pension contrib
  for (let i = 0; i < FG_x.length; i++) {
    data_p.push({ x: FG_x[i], y: FG_y[i] })
  }
  var m_data_p = []
  for (let i = 0; i < FG_x.length; i++) {
    m_data_p.push({ x: FG_x[i], y: m_FG_y[i] })
  }

  return {
    type: 'line',
    data: {
      labels: x,
      // labels: linspace(0, 300000, 6),
      datasets: [
        {
          label: 'current year pension room',
          yAxisID: 'left',
          type: 'line',
          lineTension: 0,
          data: G_data,
          backgroundColor: show_patterns ? pattern.draw('diagonal', 'rgba(0,255,0,0.35)') : 'rgba(0,255,0,0.35)',
          borderColor: '#333333', // repeat(n, '#36495d'),
          borderWidth: 2
        },
        {
          steppedLine: 'before', // before/after it is the oposite of what you think
          label: 'effective marginal rate (no pension contrib)',
          yAxisID: 'right',
          type: 'line',
          lineTension: 0,
          data: m_data,
          backgroundColor: show_patterns ? pattern.draw('line-vertical', 'rgba(0,0,255,0.35)') : 'rgba(0,0,255,0.35)',
          borderColor: '#333333', // repeat(n, '#36495d'),
          borderWidth: 2
        },
        {
          label: 'gross tax (no pension contribution)',
          yAxisID: 'left',
          type: 'line',
          lineTension: 0,
          data: data,
          backgroundColor: show_patterns ? pattern.draw('circle', 'rgba(255,0,0,0.35)') : 'rgba(255,0,0,0.35)',
          borderColor: '#333333', // repeat(n, '#36495d'),
          borderWidth: 2
        },
        {
          label: 'gross tax (max pension contribution)',
          yAxisID: 'left',
          type: 'line',
          lineTension: 0,
          data: data_p,
          backgroundColor: show_patterns ? pattern.draw('square', 'rgba(255,0,255,0.35)') : 'rgba(255,0,255,0.35)',
          borderColor: '#333333', // repeat(n, '#36495d'),
          borderWidth: 2
        },
        {
          steppedLine: 'after', // before/after it is the oposite of what you think
          label: 'effective marginal rate (max pension contribution)',
          yAxisID: 'right',
          type: 'line',
          lineTension: 0,
          data: m_data_p,
          backgroundColor: show_patterns ? pattern.draw('diamond', 'rgba(255,200,0,0.35)') : 'rgba(255,200,0,0.35)',
          borderColor: '#333333', // repeat(n, '#36495d'),
          borderWidth: 2
        }
      ]
    },
    options: {
      responsive: true,
      scales: {
        xAxes: [{
          type: 'linear',
          scaleLabel: {
            display: true,
            labelString: xlabel
          },
          ticks: {
            beginAtZero: true,
            suggestedMax: 20 // x.reduce((a, b) => Math.max(a, b))
            // padding: 25,
          },
        }],
        yAxes: [
          {
            id: 'left',
            scaleLabel: {
              display: true,
              labelString: 'GBP'
            },
            ticks: {
              beginAtZero: true,
            }
          },
          {
            id: 'right',
            scaleLabel: {
              display: true,
              labelString: 'marginal rate'
            },
            position: 'right',
            ticks: {
              beginAtZero: true,
            }
          }
        ]
      }
    }
  }
}

