<template>
    <div class="tax-widget container tax-container">
        <!-- {{ chartData_A }} -->
        <!--
        <div class="row">
            <div class="col">
                <div class="alert alert-danger"> Trigger Warning: upsetting tax information and bad CSS</div>
            </div>
        </div>
        -->
        <div class="row">
            <div class="col text-left">
                <h4>
                UK Tax and Pension Allowance (excluding NI) for
                <select v-model="year" style="border: none; padding=0px; margin=0px;">
                    <option v-for="y in years" v-bind:key="y">{{ y }}</option>
                </select>
                </h4>
            </div>
        </div>
        <div class="row">
            <div class="col text-left">
                <p style="font-size:0.8em"> show patterns: <input type="checkbox" id="checkbox" v-model="show_patterns"> </p>
            </div>
        </div>
        <div class="row">
            <!-- TODO max max width or something -->
                <canvas id="tax-chart" style="margin:0;padding:0;"></canvas>
        </div>
        <hr>
        <div class="row">
            <div class="col text-left">
                <p>Notice the 80 percent marginal rate when we consider the tax policy under a situtation when we maximum pensionn contributions. You can verify the numbers by manually punching in the endpoints <a href="https://www.uktaxcalculators.co.uk/">here</a>.</p>
            </div>
        </div>
        <div class="row">
            <div class="col text-left">
                <h4>Multi-year income allocations</h4>
                <p> What does tax and income look like under different allocations over a multi-year period?  </p>
                <p> What is the optimal allocation? </p>
            </div>
        </div>
        <div class="form-row">
            <div class="form-group col-4">
            <label for="periodsinput"># Periods</label>
            <input class="form-control" v-model.number="periods" min=1 id="periodsinput" type="number">
            </div>
            <div class="form-group col-4">
            <label for="numberinput">Average Gross Annual</label>
            <input class="form-control" v-model.number="Xmean" min=0 id="numberinput" type="number" step=10000>
            </div>
            <div class="form-group col-4">
            <label for="R0input">Initial Pension Room</label>
            <input class="form-control" v-model.number="R0" min=0 id="R0input" type="number" step=10000>
            </div>
        </div>
        <div class="row">
            <div class="col-4">
                <div class='row'>
                    <div class="col">
                        <h4>Gross Income</h4>
                        <button v-on:click="reset_gross()">reset</button>
                        <div class="input-group row" v-for="(n, year) in periods" :key="year">
                            <div class="input-group-text">{{ period_calc.X[year] | toKMB }}</div>
                            <input class="myform"
                             :id="'alpha-'+year" type="range"
                              min=0 max=1 step=0.01 v-model.number="internal_alpha[year]">
                            <div class="input-group-text">{{ alpha[year] | toPercent }}</div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col">
                        <h4>Pension Contribution</h4>
                        <button v-on:click="reset_pension()">reset</button>
                        <div class="input-group row" v-for="(n, year) in periods" :key="year">
                            <div class="input-group-text">{{ period_calc.C[year] | toKMB }}</div>
                            <input class="myform" :id="'beta-'+year" type="range"
                              min=0 max=1 step=0.01 v-model.number="internal_beta[year]">
                            <div class="input-group-text">{{ beta[year] | toPercent }}</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-8 text-left">
                <h4> Total Tax: {{ this.period_calc.T | sum | toKMB }}</h4>
                <h4>Gross Income: {{ this.period_calc.X | sum | toKMB }}</h4>
                <h4>Total Contribution: {{ this.period_calc.C | sum | toKMB }}</h4>
                <canvas id="tax-chart2" style="margin:0;padding:0;"></canvas>
                <div class="col text-left">
                    <p style="font-size:0.8em"> show patterns: <input type="checkbox" id="checkbox" v-model="show_patterns"> </p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import VueJsonPretty from 'vue-json-pretty';
import { createOrUpdateChart, get_tax_funcs, linspace, chart_data_builder, tax_calc, repeat } from './tax.js';
// import _ from 'lodash';
import pattern from 'patternomaly';

// import { numeral } from 'numeral.js';

var periods = 4;
var internal_alpha = []
for (let i=0; i<periods; i++){
    internal_alpha.push(1 / (periods - i))
}
var internal_beta = repeat(periods, 1)

export default {
    components: {
        VueJsonPretty
    },
    name: 'tax-app',
    props: {
        msg: String,
    },
    data () {
        return {
            currencyMap: {'uk': 'GBP'},
            jurisdiction : "uk",
            year : "2018-2019",
            x_min : 0,
            x_max : 250000,
            // income and pension allocations stuff
            periods: periods,
            Xmean: 200000, // mean gross annual income
            R0: 0, // pension room available coming in to year 0
            internal_alpha: internal_alpha, // X_i = alpha_i * Xmean
            internal_beta: internal_beta, // y_i = beta * sum(Y[-3:]) + G(X_i)
            chart_A: '',
            chart_B: '',
            show_patterns: false,
        }
    },
    watch: {
        periods: function() {
            while (this.internal_alpha.length < this.periods) {
                this.internal_alpha.push(1)
            }
            while (this.internal_beta.length < this.periods) {
                this.internal_beta.push(1)
            }
        },
        chartData_B: function() {
            this.chart_B = createOrUpdateChart('tax-chart2', this.chartData_B, this.chart_B)
        },
        chartData_A: function() {
            this.chart_A = createOrUpdateChart('tax-chart2', this.chartData_A, this.chart_A)
        }
    },
    computed : {
        period_calc: function() {
            // returns state obj with X, R, C, T
            return tax_calc(this.periods, this.Xmean, this.R0, this.alpha, this.beta, this.funcs.F, this.funcs.G)
        },
        alpha: {
            get: function() {
                return this.internal_alpha.slice(0, this.periods)
            }
        },
        beta: {
            get: function() {
                return this.internal_beta.slice(0, this.periods)
            }
        },
        taxinfo: function() {
            return this.fetchData()
        },
        years: function() {
            return this.taxinfo.filter(x => (x.country == 'uk')).map(x => x['year'])
        },
        taxinfo_selected: function() {
            // TODO handle if many exist
            return this.taxinfo.filter(x => (x.country == 'uk') && (x.year == this.year))[0]
        },
        funcs: function() {
            // involves a lot scope, whatever
            // TODO: refactor this into a simple js, data loader and processing step to product the funcs etc below
            return get_tax_funcs(this.taxinfo_selected)
        },
        xrange: function() {
            return linspace(this.x_min, this.x_max, 10)
        },
        chartData_A: function() {
            // call the funcs and get the data you will want to plot
            // TODO use better xrange later
            // var xrange = this.funcs.x.slice()
            // xrange.push(this.x_max)
            // var y = xrange.map(this.funcs.F)
            // var m_y = xrange.map(this.funcs.m_F)

            // // pension
            // var G_xrange = this.funcs.G_x.slice()
            // G_xrange.push(this.x_max)
            // var G_y = G_xrange.map(this.funcs.G)
            var data = chart_data_builder(this.funcs, this.x_max, 'gross income (' + this.currencyMap['uk'] + ')', this.show_patterns)
            // xrange, y, m_y, G_xrange, G_y, 'gross income (' + this.currencyMap['uk'] + ')', this.show_patterns)
            // console.log(data)
            return data
        },
        chartData_B: function() {
            this.show_patterns
            let labels = []
            for (let i=0; i<this.periods; i++) { labels.push(i)}
            return {
                type: 'bar',
                data: {
                    labels : labels,
                    datasets: [
                        {
                            label: 'income',
                            data: this.period_calc.X,
                            backgroundColor: this.show_patterns ? pattern.draw('diagonal', 'rgba(0,255,0,0.35)') : 'rgba(0,255,0,0.35)',
                            borderColor: '#333333', // repeat(n, '#36495d'),
                            borderWidth: 2,
                        },
                        {
                            label: 'tax',
                            data: this.period_calc.T,
                            backgroundColor: this.show_patterns ? pattern.draw('line-vertical', 'rgba(0,0,255,0.35)') : 'rgba(0,0,255,0.35)',
                            borderColor: '#333333', // repeat(n, '#36495d'),
                            borderWidth: 2,
                        },
                        {
                            label: 'pension',
                            data: this.period_calc.C,
                            backgroundColor: this.show_patterns ? pattern.draw('circle', 'rgba(255,0,0,0.35)') : 'rgba(255,0,0,0.35)',
                            borderColor: '#333333', // repeat(n, '#36495d'),
                            borderWidth: 2,
                        },
                        {
                            label: 'room',
                            data: this.period_calc.R.slice(1),
                            backgroundColor: this.show_patterns ? pattern.draw('line', 'rgba(0,255,255,0.35)') : 'rgba(0,255,255.0.35)',
                            borderColor: '#333333', // repeat(n, '#36495d'),
                            borderWidth: 2,
                        }
                    ]
                },
                options: {
                }
            }
        }
    },
    filters: {
        sum: function(arr) {
            return arr.reduce((x, y) => x + y)
        },
        toKMB: function(num) {
            return num > 999999 ? (num/1000000).toFixed(1) + 'M' : (num > 999 ? (num/1000).toFixed(1) + 'K' : num)
        },
        toFixed: function(value) { return parseFloat(value).toFixed(2) },
        toPercent: function(value) { return parseFloat(value * 100).toFixed(0) + "%"}
    },
    methods: { // uncache-ables
        reset_pension() {
            for (let i=0; i<this.periods; i++){
                // do not DO IT THIS WAY! https://vuejs.org/2016/02/06/common-gotchas/
                // this.internal_alpha[i] = 1 / (this.periods - i)
                this.internal_beta.splice(i, 1,  1)
            }
        },
        reset_gross() {
            for (let i=0; i<this.periods; i++){
                // do not DO IT THIS WAY! https://vuejs.org/2016/02/06/common-gotchas/
                // this.internal_alpha[i] = 1 / (this.periods - i)
                this.internal_alpha.splice(i, 1,  1 / (this.periods - i))
            }
        },
        fetchData() {
            var obj = require('./taxinfo.json')
            return obj
        }
    },
    mounted() {
        this.chart_A = createOrUpdateChart('tax-chart', this.chartData_A);
        this.chart_B = createOrUpdateChart('tax-chart2', this.chartData_B);
    }
}
</script>

